/**
 * THEME MINIMAL
 */

/* AUTOCOMPLETE */
input[data-sa-theme="minimal"].ui-autocomplete {
  position: absolute;
  cursor: default;
}
* html input[data-sa-theme="minimal"].ui-autocomplete {
  width: 1px; /* without this, the menu expands to 100% in IE6 */
}

input[data-sa-theme="minimal"].ui-widget-content {
  background: none;
  border-radius: 0px;
  border: 1px solid #c8c8c8;
  box-shadow: 2px 2px 2px -1px rgba(128,128,128,0.6);
  position: absolute;
  font-family: Arial,Tahoma,"Bitstream Vera Sans",sans-serif;
  font-size: 1em;
}

/* AUTOCOMPLETE MENU */
ul[data-sa-theme="minimal"] div {
  display: block;
}
ul[data-sa-theme="minimal"].ui-menu {
  list-style: none;
  padding: 0px;
  margin: 0;
  display: block;
  float: left;
  min-width: 350px;
  background: rgba(255,255,255,0.9);

  .ui-menu {
    margin-top: -3px;
  }

  .ui-menu-item {
    margin: 0;
    padding: 0;
    float: left;
    clear: left;
    width: 100%;
    line-height: 14px;
    font-size: 12px;

    a {
      text-decoration: none;
      display: block;
      padding: 0;
      font-weight: normal;
      margin: -1px;
      border-radius: 0;

      &:hover,
      &:focus,
      &:active {
        // background: #fff;
      }
    }
  }
}

/* Interaction States */
ul[data-sa-theme="minimal"] .ui-autocomplete-fields {
  padding: 5px 6px;
  overflow: hidden;
  font-size: 2rem;
  color: #555;
  text-transform: uppercase;
}
ul[data-sa-theme="minimal"] .ui-menu-item-first {
  border-top: 1px solid #808080;
}
ul[data-sa-theme="minimal"] .ui-autocomplete-field-term {
  font-weight: normal;
  color: #101010;
}
ul[data-sa-theme="minimal"] .ui-autocomplete-field-group {
  background-position: right top;
  background-repeat: no-repeat;
  float: right;
  position: relative;
  right: 5px;
}
